html {
  scroll-behavior: smooth;
}

body {
  font-family: Rubik, sans-serif;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 767px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (width >= 768px) {
  .container {
    max-width: 768px;
  }
}

@media (width >= 1200px) {
  .container {
    max-width: 1200px;
  }
}

button {
  cursor: pointer;
  border: none;
  display: block;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

input {
  border: none;
  outline: none;
}

h1, h2, h3, h4, h5, h6, ul, p, button {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}
